import React, { Component } from 'react'

class DeliveryGrid extends Component {
  render(){
    return(
    <div className={'testimonials-area-2 pt-95 pb-95 section-bg-white'}>
        <div className={'container'}>
            <div className={'section-title-wrap-2 section-padding-none text-center mb-30'}>
                <h3 className={'section-title section-bg-white'}>Кымдан-2 с доставкой по Астане и Алматы бесплатно!</h3>
            </div>
            <div className={'testimonial-style-2'}>
                <div className={'section-title-wrap-2 section-padding-none text-center mb-20'}>
                    <p className={'delivery-text'}>Мы так же осуществляем доставку по территории Казахстана и по странам СНГ почтовым сервисом EMS.</p>
                </div>
                <div className={'section-title-wrap-2 section-padding-none text-center mb-20'}>
                    <p className={'delivery-text'}>Стоимость доставки от 1460 тенге в зависимости от веса и расстояния.</p>
                </div>
                <div className={'section-title-wrap-2 section-padding-none text-center mb-20'}>
                    <p className={'delivery-text'}>Подробнее о тарифах вы прочтёте перейдя по ссылке <a className={'red-color'} href="https://post.kz/calc"> EMS Kazpost.</a></p>
                </div>
                <div className={'blog-img mb-30'}>
                    <img src={'assets/img/delivery/ems_dostavka.jpg'} alt="ems_delivery"/>
                </div>
                <div className={'section-title-wrap-2 section-padding-none text-center mb-20'}>
                    <p className={'delivery-text'}>Ниже мы приводим Варианты расчетов стоимости отправления по разным направлениям как по Казахстану так и по направлениям  Наших постоянных клиентов</p>
                </div>
                <div className={'section-title-wrap-2 section-padding-none text-center mb-20'}>
                    <p className={'delivery-text'}>Попробуйте сами рассчитать на бесплатном сервисе<a className={'red-color'} href="https://post.kz/calc"> EMS Kazpost.</a></p>
                </div>
                <div className={'section-title-wrap-2 section-padding-none text-center mb-20'}>
                    <p className={'delivery-text'}>10 коробочек Кымдан 2 с упаковкой вести менее 2000 грамм  (2 кг.)</p>
                </div>
                <div className={'section-title-wrap-2 section-padding-none text-center mb-30'}>
                    <h3 className={'section-title section-bg-white'}>Доставка до Шымкента</h3>
                </div>
                <div className={'blog-img mb-20'}>
                    <a href="https://post.kz/calc">
                        <img src={'assets/img/delivery/dostavka_almaty.jpg'} alt="ems_delivery"/>
                    </a>
                </div>
                <div className={'section-title-wrap-2 section-padding-none text-center mb-30'}>
                    <h3 className={'section-title section-bg-white'}>Доставка до Актау</h3>
                </div>
                <div className={'blog-img mb-20'}>
                    <a href="https://post.kz/calc">
                        <img src={'assets/img/delivery/dostavka_aktau.jpg'} alt="ems_delivery"/>
                    </a>
                </div>
                <div className={'section-title-wrap-2 section-padding-none text-center mb-30'}>
                    <h3 className={'section-title section-bg-white'}>Доставка до Узбекистана</h3>
                </div>
                <div className={'blog-img mb-20'}>
                    <a href="https://post.kz/calc">
                        <img src={'assets/img/delivery/dostavka_uzbekistan.jpg'} alt="ems_delivery"/>
                    </a>
                </div>
                <div className={'section-title-wrap-2 section-padding-none text-center mb-30'}>
                    <h3 className={'section-title section-bg-white'}>Доставка до Киргизии</h3>
                </div>
                <div className={'blog-img mb-20'}>
                    <a href="https://post.kz/calc">
                        <img src={'assets/img/delivery/dostavka_kirgizia.jpg'} alt="ems_delivery"/>
                    </a>
                </div>
            </div>
        </div>
    </div>
    );
  }
}

export default DeliveryGrid;
