import React, { Component } from 'react'

class StockGrid extends Component {
  render(){
    return(
        <div className={'testimonials-area-2 pt-95 pb-95 section-bg-white'}>
        <div className={'container'}>
            <div className={'section-title-wrap-2 section-padding-none text-center mb-50'}>
                <h3 className={'section-title section-bg-white'}>ЦЕНА КЫМДАН 2</h3>
            </div>
            <div className={'testimonial-style-2'}>
                <div className={'testimonial-2-wrapper mb-50'}>
                    <div className={'testimonial-2-content'}>
                        <p>Для удобства приема Кымдан 2 Инъекции мы предлагаем Вам приобретать курсами. Один курс для тяжелых заболеваний начинается с 7 упаковок согласно рекомендованной схеме докторов из Кореи. При покупке 3 и выше курсов ( более 21 упаковок) единовременно мы рассматриваем дополнительные скидки.</p>
                    </div>
                </div>
                <div className={'testimonial-2-wrapper mb-50'}>
                    <div className={'testimonial-2-content'}>
                        <div className={'testimonial-2-name'}>
                            <h3>Купить Кымдан 2 в Ампулах для инвалидов</h3>
                        </div>
                        <p>Для инвалидов существует специальные цены , свяжитесь с нами по форме обратной связи в разделе КОНТАКТЫ Помните оригинальный продукт Кымдан 2 необходимо приобретать только у авторизованных дилеров и  представителей корейского производителя «Пуган Фармасьютикал Ко» Лтд.</p>
                    </div>
                </div>
                <div className={'testimonial-2-wrapper '}>

                    <div className={'testimonial-2-content'}>
                        <div className={'testimonial-2-name'}>
                            <h3>Купить Кымдан 2 по оптовой цене</h3>
                        </div>
                        <p>Купить корейский препарат Кымдан 2 по оптовой цене так же возможно, оптовые закупки препарата начинаются с 100 упаковок.</p>
                        <p>Свяжитесь Нами по телефону +7 708 425 26 43</p>
                        <p>Для удобства наших клиентов мы имеем возможность приема платежей в нескольких системах.</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
    );
  }
}

export default StockGrid;
