import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faSyncAlt, faShippingFast, faUserShield } from '@fortawesome/free-solid-svg-icons'

class AboutUsGrid extends Component {
  render(){
    return(
<div>
    <div className={'about-us-area pt-100 pb-100'}>
        <div className={'container'}>
            <div className={'row'}>
               <div className={'col-lg-6 col-md-5'}>
                    <div className={'overview-img text-center'}>
                        <Link to={'/shop'}>
                            <img src={'assets/img/banners/product-2.jpg'} alt={''}/>
                        </Link>
                    </div>
                </div>
                <div className={'col-lg-6 col-md-7 d-flex align-items-center'}>
                    <div className={'overview-content-2'}>
                        <h2><span>Кымдан 2</span> в Казахстане</h2>
                        <p className={'peragraph-blog'}>Кымдан 2- высокоэффективный натуральный препарат на основе многолетнего женьшеня растущего только в Северной Корее. Повышая иммунную систему помогает организму побороть множества заболеваний, в том числе от :  Аллергического  дерматита, Анемии (Малокровие), Аритмии сердца, Бессонницы (инсомния),
                        Болезни желудка, Болезни почек, Болезнь  Гастрита, Гепатита, Диабета, Пониженного Иммунитета, Кровоподтёков.</p>
                        <p> Кымдан 2 прежде всего воздействует на центры активации иммунной системы, которая в свою очередь способно к самоизлечению.
                        Редкоземельные металлы в микрозондах обеспечивают насыщение организма необходимыми микроэлементами, что обеспечивает дополнительную защиту организма.
                        Ведь давно известно, что золото в частности оказывает положительное воздействие на человека. Кымдан официально представлен данным сайтом www.kumdang-2.kz в Казахстане.    </p>
                        <div className={'overview-btn mt-45'}>
                            <Link className={'btn-style-2'} to={'/shop'}>Заказать</Link>
                        </div>
                    </div>
                </div>

            </div>
        </div>
    </div>
    <div className={'project-count-area gray-bg pt-100 pb-70'}>
        <div className={'container'}>
            <div className={'row'}>
                <div className={'col-lg-4 col-md-12 col-sm-12'}>
                    <div className={'single-count text-center mb-30'}>
                        <div className={'count-icon'}>
                          <FontAwesomeIcon icon={faShippingFast} size="5x"></FontAwesomeIcon>
                        </div>
                        <div className={'count-title'}>
                            <h2 className={'count'}>Быстрая доставка</h2>
                            <span>Доставка по Казахстану Узбекистану и Киргизии курьерской службой</span>
                        </div>
                    </div>
                </div>
                <div className={'col-lg-4 col-md-12 col-sm-12'}>
                    <div className={'single-count text-center mb-30'}>
                        <div className={'count-icon'}>
                            <FontAwesomeIcon icon={faSyncAlt} size="5x"></FontAwesomeIcon>
                        </div>
                        <div className={'count-title'}>
                            <h2 className={'count'}>Оригинальная продукция</h2>
                            <span>У нас только оригинальная продукция из Кореи</span>
                        </div>
                    </div>
                </div>
                <div className={'col-lg-4 col-md-12 col-sm-12'}>
                    <div className={'single-count text-center mb-30'}>
                        <div className={'count-icon'}>
                          <FontAwesomeIcon icon={faUserShield} size="5x"></FontAwesomeIcon>
                        </div>
                        <div className={'count-title'}>
                            <h2 className={'count'}>Защищенные платежи</h2>
                            <span>Ваши платежи надежно защищены</span>
                        </div>
                    </div>
                </div>
                
            </div>
        </div>
    </div>
</div>
    );
  }
}

export default AboutUsGrid;
