import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebookF, faInstagram, faVk } from '@fortawesome/free-brands-svg-icons'

class Footer extends Component {
  render(){
    return(
      <div className={'footer-area black-bg-2 pt-70'}>
          <div className={'footer-top-area pb-18'}>
              <div className={'container'}>
                  <div className={'row'}>
                      <div className={'col-lg-4 col-md-6 col-sm-6'}>
                          <div className={'footer-about mb-40'}>
                              <div className={'footer-logo'}>
                                  <Link to={'index.html'}>
                                      <img src={'assets/img/logo/logo-footer.png'} alt={''}/>
                                  </Link>
                              </div>
                              <p>Кымдан 2 - мощное средство для общего оздаровления организма. Эффективно оказывает влияние на иммунитет, способствуя повышению сопротивляемости и ответных реакции защитных систем организма. Спектр использования кымдана весьма широк, с успехом применяется при острых и хронических гепатитах, циррозе печени, водянке печени, панкреатите, сахарном диабете, при язвах желудка и двенадцатиперстной кишки, при гастритах, снижении иммунитета и общей слобости, при бессонницах, при ревматизме и ревмотоидных артирттах и многих других заболеваниях.</p>
                          </div>
                      </div>
                      <div className={'col-lg-2 col-md-6 col-sm-6'}>
                          <div className={'footer-widget mb-40'}>
                              <div className={'footer-title mb-22'}>
                                  <h4>Информация</h4>
                              </div>
                              <div className={'footer-content'}>
                                  <ul>
                                      <li><Link to={'/about'}>О нас</Link></li>
                                      <li><Link to={'/delivery'}>Доставка</Link></li>
                                      <li><Link to={'/stock'}>Акций</Link></li>
                                      <li><Link to={'/refundPolicy'}>Политика возвратов</Link></li>
                                      <li><Link to={'/contacts'}>Контакты</Link></li>
                                  </ul>
                              </div>
                          </div>
                      </div>
                      <div className={'col-lg-4 col-md-6 col-sm-6'}>
                          <div className={'footer-widget mb-40'}>
                              <div className={'footer-title mb-22'}>
                                  <h4>Контакты</h4>
                              </div>
                              <div className={'footer-contact'}>
                                  <ul>
                                      <li>Адрес: Астана, ул. Сейфуллина 40</li>
                                      <li>Телефон: 8 (717) 225-26-43</li>
                                      <li>Почта: <Link to={'#'}>info@kumdang-2.kz</Link></li>
                                  </ul>
                              </div>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
          <div className={'footer-bottom-area border-top-4'}>
              <div className={'container'}>
                  <div className={'row'}>
                      <div className={'col-lg-6 col-md-6 col-sm-7'}>
                          <div className={'copyright'}>
                              <p>Copyright © 2015 <Link to={'#'}>Кымдан 2</Link></p>
                          </div>
                      </div>
                      <div className={'col-lg-6 col-md-6 col-sm-5'}>
                          <div className={'footer-social'}>
                              <ul>
                                  <li><Link to={'#'}><FontAwesomeIcon icon={ faFacebookF } size="lg"/></Link></li>
                                  <li><Link to={'#'}><FontAwesomeIcon icon={ faInstagram } size="lg"/></Link></li>
                                  <li><Link to={'#'}><FontAwesomeIcon icon={ faVk } size="lg"/></Link></li>
                              </ul>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
      </div>
    );
  }
}

export default Footer;
